

/* eslint-disable */
import Previewer from 'vux/src/components/previewer/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import SwiperItem from 'vux/src/components/swiper/swiper-item.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Emotion from 'vux/src/components/wechat-emotion/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'

import { mapGetters } from 'vuex'
import * as type from '@/store/mutation-types.js'
import { getArticleById, ArticleFabulous, countViews } from '@/api/article'
import { RESPONSE_CODE } from '@/api/config'
import { getArticleCommentList, commentArticle } from '@/api/comment'
import { saveFabulous, loadFabulous, saveComment, loadComment, saveViews, loadViews } from '@/api/cache'

const list = () => ['', '']

export default {
data () {
return {
pullup: true,
mfabulous: false,
fabulousNum: '1',
loading: true,
tip: '...',
disabled: true,
showPopup: false,

tabs: list(),
currentTab: '',
tabPanelIndex: 0,
emotions: ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', 'NO', 'OK', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''],
textAreaValue: "",
showToast: false,
commentList: null,
previewerList: [],
previewerIndex: 0,
options: {
index: 0
}
}
},
computed: {
...mapGetters([
'news'
])
},
created() {
if(!this.news.id){
this.$router.back()
return
}

setTimeout(() => {
if (!this.$refs.articleDetailScroll) {
return
}
this.$refs.articleDetailScroll.refresh()
}, 500)


let fabulousCache = loadFabulous({
id: this.news.id
})
if( fabulousCache.id) {
this.mfabulous = true
this.fabulousNum = Number(fabulousCache.fabulous) < Number(this.news.fabulous) ? this.news.fabulous : fabulousCache.fabulous
}


if( !this.mfabulous) {
this.fabulousNum = this.news.fabulous === null ? '1' : this.news.fabulous
}


this._getArticleComment()


let cacheViews = loadViews({
id: this.news.id
})

if(!cacheViews.id) {
this.news.views = parseInt(this.news.views)+1


countViews(this.news.id).then((res) => {
if (res.code == RESPONSE_CODE) {
saveViews({
id: this.news.id
})
}
});
}
},
methods: {
submitComment(){
if( this.textAreaValue.length < 3) {

this.$vux.toast.show({
text: '3',
position: 'middle',
time: 1000,
type: 'warn'
})
return
}

this.showPopup = false

let cacheComment = loadComment({
id: this.news.id
})

let times = 1
if( cacheComment.id ) {
times += cacheComment.times + 1
}

if( times >5) {
this.$vux.toast.text( '','middle')
return
}

commentArticle(this.news.id,this.textAreaValue).then((res)=>{
if (res.code == RESPONSE_CODE) {
this.commentList.splice(0,0,res.data)
saveComment({
id: this.news.id,
times: times
})
}else {
this.$vux.toast.text( '','top')
}
})
},
selectEmotion(emo){

},
commentTextArea(event){

},
commentTab() {
this.showPopup = true
},
fabulous() {



if(!this.mfabulous){


this.fabulousNum = Number(this.fabulousNum) + 1

this.mfabulous = true

ArticleFabulous(this.news.id).then((res)=>{
if (res.code === RESPONSE_CODE) {


saveFabulous({
id: this.news.id,
fabulous: this.fabulousNum
})
}
})


}else {
this.$vux.toast.text( '',   'middle')
}
},
_getArticleDetail(id) {
getArticleList(id).then((res) => {
if (res.code === RESPONSE_CODE) {
this.setNews(res.data)
}
})
},
_getArticleComment() {

this.page = 0
this.size = 6
getArticleCommentList(this.news.id,this.page,this.size).then((res)=>{

if (res.code === RESPONSE_CODE) {
this.commentList = res.data.list
this.loading = res.data.hasNext
this.tip = this.loading ? '...' : (res.data.list.length === 0 ? '' :'~')
}else {
this.$vux.toast.text( '',   'top')
}
})
},
_getNextArticleComment() {

if (!this.loading) {
return
}
this.page++
getArticleCommentList(this.news.id,this.page,this.size).then((res)=>{

if (res.code === RESPONSE_CODE) {
this.commentList = this.commentList.concat(res.data.list)
this.loading = res.data.hasNext
this.tip = this.loading ? '...' : '~'
}else {
this.$vux.toast.text( '',   'top')
}
})
},
resizeHeight () {
if (!this.$refs.articleDetailScroll) {
return
}

this.$refs.articleDetailScroll.refresh()

this._getNextArticleComment()


},
back () {
this.$router.back()
},
proxyImage(e) {

if(e.target.tagName != 'IMG') {
return
}


if(this.previewerList.length != 0){
this.options.index = Number(e.target.alt)
this.$refs.previewer.show(this.options.index)
return
}

let imgs=document.getElementById("content").getElementsByTagName("img")

if(imgs.length === 0){
return
}

for(var i=0;i<imgs.length;i++){
imgs[i].setAttribute("class","previewer")
imgs[i].setAttribute("alt",i)
this.previewerList.push({
src: imgs[i].src
})
}
this.options.index = Number(e.target.alt)
this.$refs.previewer.show(this.options.index)

}
},
components: {
Previewer,
Emotion,
XButton,
Popup,
Tabbar,
TabbarItem,
Swiper,
SwiperItem,
Tab,
TabItem,
XInput,
Group,
XTextarea,
Blur

}
}



